import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StatesListItem } from '../app-descriptors/descriptor-states';
import { Observable } from 'rxjs';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';
import { Title, Meta } from '@angular/platform-browser';
import { MetaDescriptors } from '../app-descriptors/meta-descriptors';
import { STATE_LIST } from '../app-values/states-values';
import { VILLAGE_LIST } from '../app-values/content-villages';
import { VillageDescriptor } from '../app-descriptors/village-descriptor';
import { GLOBAL_LIST_CONTENT } from '../app-values/properties-village';
import { ExtraPictures } from '../global-properties/extra-pixture-generator';
import { HouseDescription } from '../app-descriptors/house-description';
import { HOUSE_DESCRIPTION_LIST } from '../app-values/house-description';
import {
  AccessibilityConfig, Action, AdvancedLayout, ButtonEvent, ButtonsConfig, ButtonsStrategy, ButtonType, Description, DescriptionStrategy, GalleryService,
  DotsConfig, GridLayout, Image, ImageModalEvent, LineLayout, PlainGalleryConfig, PlainGalleryStrategy, PreviewConfig
} from 'angular-modal-gallery';
import { DETAIL_HOUSE_LIST } from '../app-values/detail-house';
import { VillagePromotion, VILLAGE_PROMOTION_LIST } from '../app-values/village-promotions';


@Component({
  selector: 'app-app-village',
  templateUrl: './app-village.component.html',
  styleUrls: ['./app-village.component.css']
})
export class AppVillageComponent implements OnInit {

  state:string;
  village:string;
  pathpictures:string;
 
  stateSource: StatesListItem;
  villageProperties: VillageDescriptor;
  
  houseDescription: HouseDescription[];
  locationPicture:Image[];
  villagepromotion: VillagePromotion;

  //Image Configuration
  GalleryGlobal :Description;
  plainGalleryRow: PlainGalleryConfig;
  uniqueGalleryRow: PlainGalleryConfig;

  location;
  extraPictures;
  
  //Observable responsive
  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
  .pipe( map(result => result.matches));

  GalleryGlobalConfig: Observable<PlainGalleryConfig> = this.breakpointObserver.observe(Breakpoints.Handset)
  .pipe(map(result => {
      if(result.matches)return this.extrapictures.setPlainGlobalResponsive();
      else return this.extrapictures.setPlainGlobalDesktop(this.villageProperties.extrapic.length)
    })
  );

  GalleryTesponsive: Observable<PlainGalleryConfig> = this.breakpointObserver.observe(Breakpoints.Handset)
  .pipe(
    map(result => {
      if(result.matches)return this.extrapictures.setGalleryResponsive();
      else return this.extrapictures.setGalleryDesktop();
    })
  );

  constructor(
    private breakpointObserver: BreakpointObserver,
    private route: ActivatedRoute,
    private router: Router,
    private title: Title,
    private metadescriptors:Meta,
    private metaproperties:MetaDescriptors,
    private extrapictures:ExtraPictures,
  ) { 
  }

  
  ngOnInit() {
    this.route.params.subscribe(
      params => {
       this.setGlobalSetting(params['state'], params['village']);
       this.setMetaDescriptors();
        this.pathpictures = 'assets/sources/' + this.stateSource.id + '/' + this.villageProperties.id + "/";
        this.putAsignation();
     this.setPictureConfiguration();
  });
     
  
  }

  validateItem(object){
    if(object === undefined)
      this.router.navigate(['/']);
  }

  setPictureConfiguration(){
    this.GalleryGlobal = this.extrapictures.setGlobalDescription();
    this.plainGalleryRow= this.extrapictures.setPlainGalleryRow();
    this.uniqueGalleryRow = this.extrapictures.setUniqueGalery();
  }

  setGlobalSetting(state:string, village:string){
    this.state = state;
    this.village =village;
    
    this.stateSource = STATE_LIST.filter(x=>x.path == this.state)[0];
    this.validateItem(this.stateSource);

    this.villageProperties = VILLAGE_LIST.filter(x=>x.id == this.village)[0]
    this.validateItem(this.villageProperties);

    this.villagepromotion = VILLAGE_PROMOTION_LIST.find(x=>x.idvillage == this.village);
  }

  setMetaDescriptors(){
      let m_popertiesthi = this.metaproperties.SetProperties('Village',this.villageProperties.name, this.villageProperties.place);
      this.title.setTitle(m_popertiesthi.title);
      this.metadescriptors.addTags(m_popertiesthi.metatags,true);
      this.metadescriptors.addTags([{name:"keywords", desciption:"comprar,casa,departamento,crédito," + this.stateSource.name}],true)
      this.houseDescription  = HOUSE_DESCRIPTION_LIST.filter(x=>x.idvalley ==  this.village).sort(); 
  }

  putAsignation(){    
    this.houseDescription.forEach(x=> { 
      let numeric=0; 
      x.images.push(
        new Image(numeric++,{ img: this.pathpictures + x.id+ '_fecade.jpg',description: x.name + ' - Fachada'}),
        new Image(numeric++,{ img: this.pathpictures + x.id+ '_floorplane.jpg',description: x.name + ' - Plano'}),
      );
      x.characts = DETAIL_HOUSE_LIST.filter(y=>y.idparent == x.id && y.idvillage == this.village);
    });

    this.villageProperties.globallist = GLOBAL_LIST_CONTENT.filter(x=>x.id == this.villageProperties.id);
    this.villageProperties.extrapic = this.extrapictures.getPictureList(this.villageProperties.id, this.pathpictures);
    this.locationPicture = [new Image(1,{img:this.pathpictures +'/location.jpg', description:'Ubicación'})];
  }
}
