import { NgtUniversalModule } from '@ng-toolkit/universal';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule, LOCALE_ID } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { MainNavComponent } from './main-nav/main-nav.component';
import { LayoutModule } from '@angular/cdk/layout';
import { MatToolbarModule, MatButtonModule, MatSidenavModule, MatIconModule, MatListModule, MatGridListModule, MatCardModule, MatMenuModule, MatBadgeModule } from '@angular/material';
import { HomePageComponent } from './home-page/home-page.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MainFooterComponent } from './main-footer/main-footer.component';

//Modal image
import { ModalGalleryModule } from 'angular-modal-gallery';
import { MainButtonsComponent } from './main-buttons/main-buttons.component';
import { AppVillageComponent } from './app-village/app-village.component';
import {MatExpansionModule} from '@angular/material/expansion';
import { AppFinancingComponent } from './app-financing/app-financing.component';
import { AppMapsComponent } from './app-maps/app-maps.component';
import { AppAboutComponent } from './app-about/app-about.component';
import { AppOnsaleComponent } from './app-onsale/app-onsale.component';

import { registerLocaleData } from '@angular/common';
import localeesMx from '@angular/common/locales/es-MX';
registerLocaleData(localeesMx);

@NgModule({
  declarations: [
    AppComponent,
    MainNavComponent,
    HomePageComponent,
    MainFooterComponent,
    MainButtonsComponent,
    AppVillageComponent,
    AppFinancingComponent,
    AppMapsComponent,
    AppAboutComponent,
    AppOnsaleComponent
  ],
  imports:[
 CommonModule,
NgtUniversalModule, 
    
    AppRoutingModule,
    LayoutModule,
    MatToolbarModule,
    MatButtonModule,
    MatSidenavModule,
    MatIconModule,
    MatListModule,
    MatGridListModule,
    MatCardModule,
    MatMenuModule,
    BrowserAnimationsModule,
    BrowserModule,
    ModalGalleryModule.forRoot(),
    MatExpansionModule,
    MatBadgeModule,
  ],
  providers: [{provide: LOCALE_ID, useValue: 'es-MX' }],
})
export class AppModule { }
