import { Component, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { Breakpoints, BreakpointState, BreakpointObserver } from '@angular/cdk/layout';
import { GlobalItems } from '../global-properties/global.items';
import { Observable } from 'rxjs';
import { Meta, Title } from '@angular/platform-browser';
import { MetaDescriptors } from '../app-descriptors/meta-descriptors';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {
  colsdivide:number;

  desciptors:GlobalItems[] = [
    new GlobalItems({name:'EDUCACIÓN',detail:'Desde jardín de niños hasta Universidad.', icon:'school',order:1}),
    new GlobalItems({name:'COMERCIO',detail:'Supermercados, Plazas Comerciales y Tiendas de conveniencia.', icon:'shopping_cart',order:1}),
    new GlobalItems({name:'ÁREAS VERDES',detail:'Jardines vecinales y parque urbano.', icon:'nature_people',order:1}),
    new GlobalItems({name:'ÁREAS RECREATIVAS',detail:'Multicanchas, área de ejercicio y pista para trotar.', icon:'directions_bike',order:1})
  ]
  

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
  .pipe(
    map(result => result.matches)
  );

  constructor(
    private breakpointObserver: BreakpointObserver,
    private title: Title,
    private metadescriptors:Meta,
    private metaproperties:MetaDescriptors
  ) {}

  ngOnInit(){
    this.colsdivide = 4;

    //Meta descriptors
    let m_popertiesthi = this.metaproperties.SetProperties('Index');
    this.title.setTitle(m_popertiesthi.title);
    this.metadescriptors.addTags(m_popertiesthi.metatags,true);
  }
}
