import { Component, OnInit } from '@angular/core';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { NavegationGenerator, Navegation } from '../global-properties/navigation-generator';
import { fadeAnimation } from '../app-animation';

@Component({
  selector: 'app-main-nav',
  templateUrl: './main-nav.component.html',
  styleUrls: ['./main-nav.component.css'],
  animations: [fadeAnimation],
})
export class MainNavComponent implements OnInit {
  
  propertiesNavegation:Navegation[];

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );
    
  constructor(
    private breakpointObserver: BreakpointObserver,
    private navegationItems: NavegationGenerator
  ) {}
  
  ngOnInit(){
      this.propertiesNavegation = this.navegationItems.GetNavegation();
  }

}
  