import { Component, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { Breakpoints, BreakpointState, BreakpointObserver } from '@angular/cdk/layout';
import { PROMOTION_LIST } from '../app-values/village-promotions';
import { registerLocaleData } from '@angular/common';
import localeEs from '@angular/common/locales/es-MX';
import { Observable } from 'rxjs';
import { Title, Meta } from '@angular/platform-browser';
import { MetaDescriptors } from '../app-descriptors/meta-descriptors';
registerLocaleData(localeEs, 'es-MX');


@Component({
  selector: 'app-app-onsale',
  templateUrl: './app-onsale.component.html',
  styleUrls: ['./app-onsale.component.css']
})
export class AppOnsaleComponent implements OnInit{
  onslaeActive = PROMOTION_LIST.filter(x=>x.visible )

  //Observable responsive
  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
  .pipe( map(result => result.matches));


  /** Based on the screen size, switch from standard to one column per row */
  constructor(
    private breakpointObserver: BreakpointObserver,
    private title: Title,
    private metadescriptors:Meta,
    private metaproperties:MetaDescriptors
  ) {}

  ngOnInit(){
    //Meta descriptors
    let m_popertiesthi = this.metaproperties.SetProperties('Promociones');
    this.title.setTitle(m_popertiesthi.title);
    this.metadescriptors.addTags(m_popertiesthi.metatags,true);
  }
}
