import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomePageComponent } from './home-page/home-page.component';
import { AppVillageComponent } from './app-village/app-village.component';
import { AppFinancingComponent } from './app-financing/app-financing.component';
import { AppAboutComponent } from './app-about/app-about.component';
import { AppOnsaleComponent } from './app-onsale/app-onsale.component';

const routes: Routes = [
  {path:'',component:HomePageComponent},
  {path:'propiedad/:state/:village',component:AppVillageComponent},
  {path:'financiamiento',component:AppFinancingComponent},
  {path:'contacto',component:AppAboutComponent},
  {path:'promociones',component:AppOnsaleComponent},
  {path: '**', redirectTo: ''},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
