import { Injectable } from "@angular/core";
import { DescriptorProperties } from "./descriptor-properties";

@Injectable({providedIn:'root'})
export class MetaDescriptors{
    constructor(){};

    SetProperties (page:string, desription?:string, location?:string){
        let title:string;
        let metalist:any[];

        if(page == 'Index'){
            title = 'Comprar Casas o Departamento - Distintos lugares de México | Elige tu mejor opción';
            metalist = [{name:'description',
            content:'Si buscas comprar una casa o departamento, tenemos a tu disposición más de 50 desarrollos \
de vivienda en 14 estados de la república y 17 de las ciudades más importantes del país.'},
            {name:"keywords", content:"comprar,casas,departamento,mexico,crédito"},
            {name:"language", content:"es-mx"},
            {name:"robots", content:"index,follow"},
            {name:"og:title", content:"Comprar casa o Departamento - Elije tu mejor opción"},
            {name:"og:image", content:"/asset/img/global/home.png"},
        ];
        }
        if(page == 'Financiemiento'){
            title = 'Comprar Casas o Departamento - Finaciemiento';
            metalist = [{name:'description',
            content:'Créditos INFONAVIT, Segundo crédito, FOVISSSTE, entre otros.\
Con gusto te podemos asesorar y guiar de manera gratuita en el trámite de tu crédito \
para seleccionar el que mejor se adapte a tus posibilidades y necesidades.'},
            {name:"keywords", content:"comprar,casas,departamento,mexico,crédito,financiamiento"},
            {name:"language", content:"es-mx"},
            {name:"robots", content:"noindex,follow"},
            {name:"og:title", content:"Comprar casa o Departamento - Financiamiento"},
            {name:"og:image", content:"/asset/img/global/home.png"},
];
        }
        if(page == 'Promociones'){
            title = 'Comprar Casas o Departamento - Promociones';
            metalist = [{name:'description',
            content:'Tenemos increíbles promociones en la compra de tú casa, acercate a nosotros \
y con gusto te podemos asesorar'},
            {name:"keywords", content:"comprar,casas,departamento,mexico,crédito,financiamiento"},
            {name:"language", content:"es-mx"},
            {name:"robots", content:"noindex,follow"},
            {name:"og:title", content:"Comprar casa o Departamento - Promociones"},
            {name:"og:image", content:"/asset/img/global/home.png"},
];
        }
        if(page == 'Contacto'){
            title = 'Comprar Casas o Departamento - Conocenos';
            metalist = [{name:'description',
            content:'Mi nombre es Lic. Alfonso Eduardo Bueno Rojí. \
En este sitio podrán encontrar algunas de las opciones que tenemos preparadas para ustedes.'},
            {name:"keywords", content:"comprar,casas,departamento,mexico,crédito,financiamiento"},
            {name:"language", content:"es-mx"},
            {name:"robots", content:"noindex,follow"},
            {name:"og:title", content:"Comprar casa o Departamento - Contacto"},
            {name:"og:image", content:"/asset/img/global/home.png"},
];
        }
        if(page == 'Village'){
            title = 'Comprar Casas o Departamento - ' + desription + ' | ' + location ;
            metalist = [{name:'description',
            content:'Si estas buscando comprar una casa o departamento en '+ location +' tenemos las mejores ofertas \
en '+desription+', Con gusto te podemos asesorar y guiar de manera gratuita'},
            {name:"language", content:"es-mx"},
            {name:"robots", content:"noindex,follow"},
            {name:"og:title", content:"Comprar casa o Departamento - " + location},
            {name:"og:image", content:"/asset/img/global/home.png"},
];
        }
        
        let descriptorproperties : DescriptorProperties = {
            title : title,
            metatags: metalist
        };
        

        return descriptorproperties;
    }
}