
export class GlobalItems{
    name:string;
    detail:string;
    icon:string;
    order:number;
  
    constructor(options:({
      name?:string;
      detail?:string;
      icon?:string;
      order?:number
    })={}){
      this.name = options.name || '';
      this.detail = options.detail || '';
      this.icon = options.icon || '';
      this.order = options.order === undefined ? 0:options.order;
    }
}