import { Component } from '@angular/core';
import { map } from 'rxjs/operators';
import { Breakpoints, BreakpointState, BreakpointObserver } from '@angular/cdk/layout';
import { Meta, Title } from '@angular/platform-browser';
import { MetaDescriptors } from '../app-descriptors/meta-descriptors';
import { Observable } from 'rxjs';
import { GlobalItems } from '../global-properties/global.items';

@Component({
  selector: 'app-app-about',
  templateUrl: './app-about.component.html',
  styleUrls: ['./app-about.component.css']
})
export class AppAboutComponent {
  desciptors:GlobalItems[] = [
    new GlobalItems({name:'Télefono',detail:'44-39-21-77', icon:'phone',order:1}),
    new GlobalItems({name:'Celular',detail:'55-19-58-27-75', icon:'phone',order:1}),
    new GlobalItems({name:'Whatsapp',detail:'55-46-10-78-93', icon:'phone',order:1}),
    new GlobalItems({name:'Email',detail:'abueno@sadasi.com.', icon:'mail',order:1})
  ]

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
  .pipe(
    map(result => result.matches)
  );

  constructor(
    private breakpointObserver: BreakpointObserver,
    private title: Title,
    private metadescriptors:Meta,
    private metaproperties:MetaDescriptors
  ) {}

  ngOnInit(){
    //Meta descriptors
    let m_popertiesthi = this.metaproperties.SetProperties('Contacto');
    this.title.setTitle(m_popertiesthi.title);
    this.metadescriptors.addTags(m_popertiesthi.metatags,true);
  }

}
