import { STATE_LIST } from "../app-values/states-values";
import { Injectable } from "@angular/core";
import { VILLAGE_LIST } from "../app-values/content-villages";

@Injectable({providedIn:'root'})
export class NavegationGenerator{
    GetNavegation(){
        let locations: Navegation[] = [];
        let villageslocation : Navegation[] = [];
        let villages: Navegation[]=[];

        VILLAGE_LIST.sort((a, b) => a.name > b.name ? 1 : -1).forEach(x => {
            villageslocation.push({name: x.name,path:x.id , parent:x.idstate,children:[] });
        });

        STATE_LIST.sort((a, b) => a.name > b.name ? 1 : -1).forEach(x=>{
            villages = villageslocation.filter(y=>y.parent == x.id);
            if(villages.length>0){
                locations.push({name:x.name,parent:'',path:x.path,children: villages});
            }
        });
        return locations;
    }
}


export class Navegation{
    name:string;
    parent:string;
    path:string;
    children:Navegation[];
}